<template>
	<v-card class="mt-3" elevation="6">
		<v-card-title class="py-2">
			<div>User Aktif</div>
			<v-spacer></v-spacer>
			<div>
				<v-btn 
	      	style="border-radius: 2px 0 0 2px"
	      	small
	      	@click="aktivasi = true"
	      	:color="aktivasi === true?'success':'default'"
	      	depressed>
	      	last week
	      </v-btn>
	      <v-btn 
	      	style="border-radius: 0 2px 2px 0"
	      	small
	      	@click="aktivasi = false"
	      	:color="aktivasi === false?'success':'default'"      	
	      	:dark="aktivasi === false?true:false" 
	      	depressed>
	      	all month
	      </v-btn>
			</div>
		</v-card-title>
		<v-divider></v-divider>
		<template>
			<v-data-table
				:headers="header"
				:items="data"
				:items-per-page="10"
				hide-default-footer>	
				<template v-slot:no-data>
		      <v-alert type="info" class="mt-4">
		        <div>Data tidak ditemukan</div>
		      </v-alert>
		    </template>
		    <template v-slot:item.no="{item}">
	        {{data.map(x => x.asal).indexOf(item.asal)+1}}
	      </template>
	      <template v-slot:item.name="{item}">
	      	<div class="d-flex">
	      		<v-avatar size="40">
				      <img
				        :src="require('@/assets/'+item.name.avatar)"
				        alt="John">
				    </v-avatar>
				    <div class="ml-3">
				    	<div class="font-weight-bold">{{ item.name.name }}</div>
				    	<div class="caption grey--text">{{ item.name.desc }}</div>
				    </div>
	      	</div>
	      </template>
	      <template v-slot:item.status="{item}">
	      	<v-chip small label color="success">
	      		{{ item.status }}
	      	</v-chip>
	      </template>
				<template v-slot:item.action="{item}">
					<v-btn 
						color="primary"
						depressed
						small 
						@click="$router.push({ name: 'preview-ptn' })">
						details
					</v-btn>
				</template>		
			</v-data-table>
		</template>
	</v-card>
</template>

<script>
	export default{
		data: () => ({
			aktivasi: true,
			header: [
				{ text: 'No', value: 'no', sortable: false },
				{ text: 'Nama', value: 'name', sortable: false },
				{ text: 'Kota', value: 'prodi', sortable: false },
				{ text: 'Status', value: 'status', sortable: false },
				{ text: 'Penjualan', value: 'user', sortable: false },
				{ text: 'Action', value: 'action', align: 'center', sortable: false }
			],
			data: [
				{
					name: {
						avatar: 'avatar.jpg',
						name: 'Rizky Rosyid H',
						desc: 'Web Frontend'
					},
					status: 's',
					prodi: 's',
					user: 's',
				}
			]
		})
	}
</script>
